import * as React from "react";

import {
  PrimaryButton,
  DefaultButton
} from "office-ui-fabric-react/lib/Button";
import {
  Dialog,
  DialogType,
  DialogFooter
} from "office-ui-fabric-react/lib/Dialog";

import { Strings } from "../strings";
import { ISelfTestResultDialogProps } from "../common/ISelfTestResultDialogProps";
import {
  StyledSelfTestResultItemWrapper,
  StyledSelfTestResultItemCell,
  StyledSelfTestResultItemKey,
  StyledSelfTestResultItemValue
} from "./styled";

const asUrl = (url: string): string =>
  !!url ? `<a href="${url}" target="_blank">Read online</a>` : "";

export const SelfTestResultDialog: React.FunctionComponent<ISelfTestResultDialogProps> = (
  props: ISelfTestResultDialogProps
) => {
  const { selfTestResult, onDismiss } = props;
  return (
    <React.Fragment>
      <Dialog
        hidden={false}
        dialogContentProps={{
          type: DialogType.normal,
          title: Strings.titleSelfTestResult
        }}
        modalProps={{
          isBlocking: false
        }}
        onDismiss={(ev: React.MouseEvent<HTMLButtonElement>) => onDismiss()}
      >
        <StyledSelfTestResultItemWrapper>
          <StyledSelfTestResultItemCell>
            <StyledSelfTestResultItemKey>Timestamp</StyledSelfTestResultItemKey>
            <StyledSelfTestResultItemValue
              dangerouslySetInnerHTML={{ __html: selfTestResult.timestamp }}
            />
          </StyledSelfTestResultItemCell>
          <StyledSelfTestResultItemCell>
            <StyledSelfTestResultItemKey>Title</StyledSelfTestResultItemKey>
            <StyledSelfTestResultItemValue
              dangerouslySetInnerHTML={{ __html: selfTestResult.title }}
            />
          </StyledSelfTestResultItemCell>
          <StyledSelfTestResultItemCell>
            <StyledSelfTestResultItemKey>Category</StyledSelfTestResultItemKey>
            <StyledSelfTestResultItemValue
              dangerouslySetInnerHTML={{ __html: selfTestResult.category }}
            />
          </StyledSelfTestResultItemCell>
          <StyledSelfTestResultItemCell>
            <StyledSelfTestResultItemKey>Passed</StyledSelfTestResultItemKey>
            <StyledSelfTestResultItemValue
              dangerouslySetInnerHTML={{
                __html: selfTestResult.passed ? "Yes" : "No"
              }}
            />
          </StyledSelfTestResultItemCell>
          <StyledSelfTestResultItemCell>
            <StyledSelfTestResultItemKey>Severity</StyledSelfTestResultItemKey>
            <StyledSelfTestResultItemValue
              dangerouslySetInnerHTML={{ __html: selfTestResult.severity }}
            />
          </StyledSelfTestResultItemCell>
          <StyledSelfTestResultItemCell>
            <StyledSelfTestResultItemKey>Message</StyledSelfTestResultItemKey>
            <StyledSelfTestResultItemValue
              dangerouslySetInnerHTML={{ __html: selfTestResult.message }}
            />
          </StyledSelfTestResultItemCell>
          <StyledSelfTestResultItemCell>
            <StyledSelfTestResultItemKey>More info</StyledSelfTestResultItemKey>
            <StyledSelfTestResultItemValue
              dangerouslySetInnerHTML={{
                __html: asUrl(selfTestResult.more_info)
              }}
            />
          </StyledSelfTestResultItemCell>
        </StyledSelfTestResultItemWrapper>
        <DialogFooter>
          <PrimaryButton
            onClick={() => onDismiss()}
            text={Strings.capOkButton}
          />
        </DialogFooter>
      </Dialog>
    </React.Fragment>
  );
};
