import * as React from "react";

import {
  StyledTableCaption,
  StyledTable,
  StyledTableHeader,
  StyledTableIntro,
  StyledTableRowGroup,
  StyledTableRow,
  StyledTableCell,
  StyledLabel,
  StyledRowifier,
  StyledCheckbox,
  StyledErrorFor,
  StyledP
} from "./styled";

import { IEnv } from "./Wizard";
import { Settings } from "../common/Settings";
import { SettingsException } from "../common/Exceptions";
import { SaveStatus, Size, PluginVersion } from "../common/Enums";
import { ValidatableTextInput } from "./ValidatableTextInput";
import { Strings } from "../strings";
import { CalloutButtonWp } from "./CalloutButtonWp";
import { errorFor } from "../common/Helpers";
import { Footer } from "./Footer";

import { Checkbox } from "office-ui-fabric-react/lib/Checkbox";
import { PrimaryButton } from "office-ui-fabric-react/lib/Button";
import { IAdditionalInfoDialogProps } from "../common/IAdditionalInfoDialogProps";
import { AdditionalInfoDialog } from "./AdditionalInfoDialog";
import { VersionSwitch } from "./VersionSwitch";

export interface IMiscellaneousTabProps {
  env: IEnv;
  settings: Settings;
}

export interface IMiscellaneousTabState {
  leeway: number;
  skipHostVerification: boolean;
  skipNonceVerification: boolean;
  skipUserRegisterAction: boolean;
  skipWpLoginAction: boolean;
  useIdTokenParserV1: boolean;
  redirectToLoginV2: boolean;
  validateOnPluginsLoaded: boolean;
  // ---
  errors: SettingsException[];
  saveStatus: SaveStatus;
  additionalInfo: IAdditionalInfoDialogProps | null;
}

export class MiscellaneousTab extends React.Component<
  IMiscellaneousTabProps,
  IMiscellaneousTabState
> {
  public constructor(props: IMiscellaneousTabProps) {
    super(props);
    const { settings } = this.props;
    this.state = {
      leeway: settings.leeway,
      skipHostVerification: settings.skipHostVerification,
      skipNonceVerification: settings.skipNonceVerification,
      skipUserRegisterAction: settings.skipUserRegisterAction,
      skipWpLoginAction: settings.skipWpLoginAction,
      useIdTokenParserV1: settings.useIdTokenParserV1,
      redirectToLoginV2: settings.redirectToLoginV2,
      validateOnPluginsLoaded: settings.validateOnPluginsLoaded,
      errors: [],
      saveStatus: SaveStatus.initial,
      additionalInfo: null
    };
  }

  public componentDidUpdate(prevProps: IMiscellaneousTabProps): void {
    if (this.props.settings === prevProps.settings) return;
    const { settings } = this.props;
    this.setState({
      leeway: settings.leeway,
      skipHostVerification: settings.skipHostVerification,
      skipNonceVerification: settings.skipNonceVerification,
      skipUserRegisterAction: settings.skipUserRegisterAction,
      skipWpLoginAction: settings.skipWpLoginAction,
      useIdTokenParserV1: settings.useIdTokenParserV1,
      redirectToLoginV2: settings.redirectToLoginV2,
      validateOnPluginsLoaded: settings.validateOnPluginsLoaded,
      additionalInfo: null
    });
  }

  public render(): React.ReactElement<IMiscellaneousTabProps> {
    return (
      <StyledTable>
        <StyledTableCaption>
          <StyledTableHeader>Miscellaneous</StyledTableHeader>
          <StyledTableIntro
            dangerouslySetInnerHTML={{
              __html: `Use this tab to configure general plugin behaviour.`
            }}
          />
        </StyledTableCaption>
        <StyledTableRowGroup>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Don't fire wp-login action</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox
                    onChange={this.toggleSkipWpLoginAction}
                    checked={this.state.skipWpLoginAction}
                  />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={"skip-wp-login-action"}
                  info={{
                    title: "Don't fire wp-login action",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/skip-wp-login-action/"
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}
          >
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Don't fire user_register action</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={this.toggleSkipUserRegisterAction}
                      checked={this.state.skipUserRegisterAction}
                    />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={"skip-user-register-action"}
                    info={{
                      title: "Don't fire user_register action",
                      readMore: {
                        title: "Read online",
                        url: "https://www.wpo365.com/skip-user-register-action/"
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Validate on plugins_loaded</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox
                    onChange={this.toggleValidateOnPluginsLoaded}
                    checked={this.state.validateOnPluginsLoaded}
                  />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={"validate-on-plugins-loaded"}
                  info={{
                    title: "Validate on plugins_loaded",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/validate-on-plugins-loaded/"
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Use older ID token parser</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox
                    onChange={this.toggleUseIdTokenParserV1}
                    checked={this.state.useIdTokenParserV1}
                  />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={"use-id-token-parser-v1"}
                  info={{
                    title: "Use ID token parser v1",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/use-id-token-parser-v1/"
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Skip NONCE verification</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox
                    onChange={this.toggleSkipNonceVerification}
                    checked={this.state.skipNonceVerification}
                  />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={"skip-nonce-verification"}
                  info={{
                    title: "Skip NONCE verification",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/skip-nonce-verification/"
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Skip SSL host verification</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox
                    onChange={this.toggleSkipHostVerification}
                    checked={this.state.skipHostVerification}
                  />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={"skip-ssl-host-verification"}
                  info={{
                    title: "Skip SSL host verification",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/skip-ssl-host-verification/"
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Leeway</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                isValid={(oldValue: any, newValue: any) => {
                  return /^\d+$/.test(newValue as string); // validate numeric input
                }}
                onValid={(newValue: any) => {
                  this.setState({
                    leeway: parseInt(newValue)
                  });
                  return;
                }}
                bound={false}
                errorMessage={Strings.errLeewayMustBeNumeric}
                defaultValue={this.state.leeway.toString()}
                value={this.state.leeway.toString()}
                placeholder={Strings.infoLeeway}
              >
                <CalloutButtonWp
                  wpSlug={"leeway-time"}
                  info={{
                    title: "Leeway time",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/leeway-time/"
                    }
                  }}
                />
              </ValidatableTextInput>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell />
            <StyledTableCell>
              <StyledRowifier>
                <PrimaryButton
                  onClick={() => this.saveConfiguration()}
                  iconProps={{
                    iconName:
                      this.state.saveStatus == SaveStatus.success
                        ? "Like"
                        : "Dislike"
                  }}
                  style={{ minWidth: "400px", minHeight: "30px" }}
                  disabled={this.state.saveStatus == SaveStatus.saving}
                >
                  Save configuration
                </PrimaryButton>
                <StyledErrorFor>
                  {errorFor(this.state.errors, "saveConfiguration").map(
                    (error: SettingsException) => (
                      <StyledP>{error.message}</StyledP>
                    )
                  )}
                  {this.state.saveStatus == SaveStatus.success
                    ? Strings.okSettingsUpdated
                    : null}
                </StyledErrorFor>
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
        </StyledTableRowGroup>
        <Footer />
        {this.state.additionalInfo && (
          <AdditionalInfoDialog
            onCancel={this.state.additionalInfo.onCancel}
            onConfirm={this.state.additionalInfo.onConfirm}
            onDismiss={this.state.additionalInfo.onDismiss}
            title={this.state.additionalInfo.title}
            subText={this.state.additionalInfo.subText}
          />
        )}
      </StyledTable>
    );
  }

  private toggleSkipNonceVerification = (): void => {
    const { skipNonceVerification } = this.state;
    if (skipNonceVerification) {
      this.setState({
        skipNonceVerification: false
      });
      return;
    }
    const confirm: IAdditionalInfoDialogProps = {
      title: Strings.titleSkipNonceVerification,
      subText: Strings.subTextSkipNonceVerification,
      onDismiss: () =>
        this.setState({
          additionalInfo: null
        }),
      onCancel: () =>
        this.setState({
          additionalInfo: null
        }),
      onConfirm: () =>
        this.setState({
          additionalInfo: null,
          skipNonceVerification: true
        })
    };
    this.setState({
      additionalInfo: confirm
    });
  };

  private toggleUseIdTokenParserV1 = (): void => {
    const { useIdTokenParserV1 } = this.state;
    if (useIdTokenParserV1) {
      this.setState({
        useIdTokenParserV1: false
      });
      return;
    }
    const confirm: IAdditionalInfoDialogProps = {
      title: Strings.titleUseIdTokenParserV1,
      subText: Strings.subTextUseIdTokenParserV1,
      onDismiss: () =>
        this.setState({
          additionalInfo: null
        }),
      onCancel: () =>
        this.setState({
          additionalInfo: null
        }),
      onConfirm: () =>
        this.setState({
          additionalInfo: null,
          useIdTokenParserV1: true
        })
    };
    this.setState({
      additionalInfo: confirm
    });
  };

  private toggleSkipHostVerification = (): void => {
    this.setState(
      (prevState: IMiscellaneousTabState, props: IMiscellaneousTabProps) => {
        return { skipHostVerification: !prevState.skipHostVerification };
      }
    );
  };

  private toggleValidateOnPluginsLoaded = (): void => {
    this.setState(
      (prevState: IMiscellaneousTabState, props: IMiscellaneousTabProps) => {
        return { validateOnPluginsLoaded: !prevState.validateOnPluginsLoaded };
      }
    );
  };

  private toggleSkipWpLoginAction = (): void => {
    this.setState(
      (prevState: IMiscellaneousTabState, props: IMiscellaneousTabProps) => {
        return { skipWpLoginAction: !prevState.skipWpLoginAction };
      }
    );
  };

  private toggleSkipUserRegisterAction = (): void => {
    this.setState(
      (prevState: IMiscellaneousTabState, props: IMiscellaneousTabProps) => {
        return { skipUserRegisterAction: !prevState.skipUserRegisterAction };
      }
    );
  };

  private async saveConfiguration(): Promise<void> {
    this.setState({ saveStatus: SaveStatus.saving });
    const { settings } = this.props;
    settings.leeway = this.state.leeway;
    settings.skipHostVerification = this.state.skipHostVerification;
    settings.skipNonceVerification = this.state.skipNonceVerification;
    settings.skipUserRegisterAction = this.state.skipUserRegisterAction;
    settings.skipWpLoginAction = this.state.skipWpLoginAction;
    settings.useIdTokenParserV1 = this.state.useIdTokenParserV1;
    settings.redirectToLoginV2 = this.state.redirectToLoginV2;
    settings.validateOnPluginsLoaded = this.state.validateOnPluginsLoaded;
    const updateResult = await Settings.updateSettings(
      this.props.env,
      settings
    );

    if (updateResult.success) {
      this.setState({
        errors: [],
        saveStatus: SaveStatus.success
      });
    } else {
      this.setState(
        (prevState: IMiscellaneousTabState, props: IMiscellaneousTabProps) => {
          return {
            errors: [
              ...prevState.errors,
              new SettingsException(
                "saveConfiguration",
                updateResult.message,
                2000
              )
            ],
            saveStatus: SaveStatus.error
          };
        }
      );
    }
  }
}
