import * as React from "react";

import {
  StyledTable,
  StyledTableCaption,
  StyledTableHeader,
  StyledTableIntro,
  StyledTableRowGroup,
  StyledTableRow,
  StyledTableCell,
  StyledRowifier,
  StyledLabel,
  StyledErrorFor,
  StyledCheckbox,
  StyledP
} from "./styled";

import { Checkbox } from "office-ui-fabric-react/lib/Checkbox";
import { PrimaryButton, IButtonProps } from "office-ui-fabric-react/lib/Button";

import { ValidatableTextInput } from "./ValidatableTextInput";
import { SettingsException } from "../common/Exceptions";
import { IEnv } from "./Wizard";
import { ListEditor } from "./ListEditor";
import { Footer } from "./Footer";
import { Settings } from "../common/Settings";
import { Strings } from "../strings";
import {
  TestStatus,
  SaveStatus,
  PluginVersion,
  Size,
  AuthScenario
} from "../common/Enums";
import { errorFor } from "../common/Helpers";
import { CalloutButtonWp } from "./CalloutButtonWp";
import {
  Dropdown,
  DropdownMenuItemType,
  IDropdownOption
} from "office-ui-fabric-react/lib/Dropdown";
import { VersionSwitch } from "./VersionSwitch";
import { Equal } from "./Equal";
import { IAdditionalInfoDialogProps } from "../common/IAdditionalInfoDialogProps";
import { AdditionalInfoDialog } from "./AdditionalInfoDialog";

export interface IAuthenticationTabProps {
  env: IEnv;
  settings: Settings;
}

export interface IAuthenticationTabState {
  applicationId: string;
  directoryId: string;
  domainHint: string;
  domainWhitelist: string[];
  nonceSecret: string;
  skipApiBasicAuthRequest: boolean;
  pagesBlacklist: string[];
  privatePages: string[];
  redirectUrl: string;
  authScenario: AuthScenario;
  rememberUser: boolean;
  sessionDuration: number;
  useV2: boolean;
  publicHomepage: boolean;
  // ---
  errors: SettingsException[];
  nonce: string;
  testStatus: TestStatus;
  saveStatus: SaveStatus;
  additionalInfo: IAdditionalInfoDialogProps | null;
}

export class AuthenticationTab extends React.Component<
  IAuthenticationTabProps,
  IAuthenticationTabState
> {
  public constructor(props: IAuthenticationTabProps) {
    super(props);
    const { settings, env } = this.props;
    this.state = {
      directoryId: settings.tenantId,
      applicationId: settings.applicationId,
      redirectUrl: settings.redirectUrl,
      domainHint: settings.domainHint,
      authScenario: settings.authScenario,
      nonceSecret: settings.nonceSecret,
      skipApiBasicAuthRequest: settings.skipApiBasicAuthRequest,
      pagesBlacklist: settings.pagesBlacklist,
      privatePages: settings.privatePages,
      domainWhitelist: settings.domainWhitelist,
      rememberUser: settings.rememberUser,
      sessionDuration: settings.sessionDuration,
      useV2: settings.useV2,
      publicHomepage: settings.publicHomepage,
      testStatus: TestStatus.initial,
      saveStatus: SaveStatus.initial,
      nonce: env.props.nonce,
      errors: [],
      additionalInfo: null
    };
  }

  public componentDidUpdate(prevProps: IAuthenticationTabProps): void {
    if (this.props.settings === prevProps.settings) return;
    const { settings } = this.props;
    this.setState({
      directoryId: settings.tenantId,
      applicationId: settings.applicationId,
      redirectUrl: settings.redirectUrl,
      domainHint: settings.domainHint,
      authScenario: settings.authScenario,
      nonceSecret: settings.nonceSecret,
      skipApiBasicAuthRequest: settings.skipApiBasicAuthRequest,
      pagesBlacklist: settings.pagesBlacklist,
      privatePages: settings.privatePages,
      domainWhitelist: settings.domainWhitelist,
      rememberUser: settings.rememberUser,
      sessionDuration: settings.sessionDuration,
      useV2: settings.useV2,
      publicHomepage: settings.publicHomepage
    });
  }

  public render(): React.ReactElement<IAuthenticationTabProps> {
    return (
      <StyledTable>
        <StyledTableCaption>
          <StyledTableHeader>
            Basic WordPress + Office 365 configuration
          </StyledTableHeader>
          <StyledTableIntro
            dangerouslySetInnerHTML={{
              __html: `Use this tab to configure the Single Sign-on authentication capability of the plugin.
              <br/><br/>
              <strong>Before you close this page</strong>
              <ol><li>Make sure that your own WordPress Administrator account is a <strong>WordPress-only account</strong> 
              so that you can always navigate to your WordPress login form e.g. <i>https://www.your-website.com/wp-login.php</i> 
              and manually log in to correct any possible misconfigurations.</li>
              <li>If you use a theme or plugin that changes the URL of the default WordPress login 
              form, you must add this new login URL to the <strong>Pages Blacklist</strong> setting below. 
              For example Woocommerce and Woffice add their own login form with permalink <i>/login/</i>.</li>
              <li>Last but not least test <strong>single sign-on</strong> as per instructions shown when you
              click <strong>Save + Test configuration</strong> or else you risk not being able to sign into 
              your website as an administrator again.</li></ol>`
            }}
          />
        </StyledTableCaption>
        <StyledTableRowGroup>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Use Azure AD v2.0</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox
                    onChange={this.toggleUseV2}
                    checked={this.state.useV2}
                  />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={"azure-ad-v2"}
                  info={{
                    title: "Use Aure AD v2.0",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/azure-ad-v2/"
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Directory (tenant) ID</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                isValid={(oldValue: any, newValue: any) => {
                  return /^[0-9a-f]{8}-[0-9a-f]{4}-[1-5][0-9a-f]{3}-[89ab][0-9a-f]{3}-[0-9a-f]{12}$/i.test(
                    newValue as string
                  );
                }}
                onValid={(newValue: any) => {
                  this.setState({
                    directoryId: newValue as string
                  });
                  return;
                }}
                bound={false}
                errorMessage={Strings.errDirectoryIdMustBeGuid}
                defaultValue={this.state.directoryId}
                value={this.state.directoryId}
                placeholder={Strings.infoDirectoryId}
              >
                <CalloutButtonWp
                  wpSlug={"azure-ad-tenant-id"}
                  info={{
                    title: "Directory ID",
                    readMore: {
                      title: "Read more",
                      url: "https://www.wpo365.com/azure-ad-tenant-id/"
                    }
                  }}
                />
              </ValidatableTextInput>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Application (client) ID</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                isValid={(oldValue: any, newValue: any) => {
                  return /^[0-9a-f]{8}-[0-9a-f]{4}-[1-5][0-9a-f]{3}-[89ab][0-9a-f]{3}-[0-9a-f]{12}$/i.test(
                    newValue as string
                  ); // validate GUID
                }}
                onValid={(newValue: any) => {
                  this.setState({
                    applicationId: newValue as string
                  });
                  return;
                }}
                bound={false}
                errorMessage={Strings.errApplicationIdMustBeGuid}
                defaultValue={this.state.applicationId}
                value={this.state.applicationId}
                placeholder={Strings.infoApplicationId}
              >
                <CalloutButtonWp
                  wpSlug={"azure-ad-application-id"}
                  info={{
                    title: "Azure AD Application ID",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/azure-ad-application-id/"
                    }
                  }}
                />
              </ValidatableTextInput>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Redirect URI</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                isValid={(oldValue: any, newValue: any) => {
                  return /^(http|https):\/\/.*\/{1}$/i.test(newValue as string);
                }}
                onValid={(newValue: any) => {
                  this.setState({
                    redirectUrl: newValue as string
                  });
                  return;
                }}
                bound={false}
                errorMessage={Strings.errRedirectUriMustBeUrl}
                defaultValue={this.state.redirectUrl}
                value={this.state.redirectUrl}
                placeholder={Strings.infoRedirectUri}
              >
                <CalloutButtonWp
                  wpSlug={"redirect-uri"}
                  info={{
                    title: "Redirect URI",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/redirect-uri/"
                    }
                  }}
                />
              </ValidatableTextInput>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Domain hint</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                isValid={(oldValue: any, newValue: any) => {
                  return !/^http/i.test(newValue as string);
                }}
                onValid={(newValue: any) => {
                  this.setState({
                    domainHint: newValue as string
                  });
                  return;
                }}
                bound={false}
                errorMessage={Strings.errDomainHintCannotStartWith}
                defaultValue={this.state.domainHint}
                value={this.state.domainHint}
                placeholder={Strings.infoDomainHint}
              >
                <CalloutButtonWp
                  wpSlug={"domain-hint"}
                  info={{
                    title: "Domain hint",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/domain-hint/"
                    }
                  }}
                />
              </ValidatableTextInput>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Authentication scenario</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <Dropdown
                  options={[
                    {
                      key: "select",
                      text: "Select",
                      itemType: DropdownMenuItemType.Header
                    },
                    { key: "intranet", text: "Intranet" },
                    { key: "internet", text: "Internet" }
                  ]}
                  selectedKey={this.state.authScenario}
                  onChange={this.changeAuthScenario}
                />
                <CalloutButtonWp
                  wpSlug={"authentication-scenario"}
                  info={{
                    title: "Authentication scenario",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/authentication-scenario/"
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Pages blacklist</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <ListEditor
                  list={this.state.pagesBlacklist}
                  onListChanged={(list: string[]) =>
                    this.setState({ pagesBlacklist: list })
                  }
                  isValid={new RegExp(/^((?!http|\/wp-admin|wp-admin))/i)}
                  errorMessage={Strings.errBlacklistedPageCannotStartWith}
                  placeholder={Strings.infoPagesBlackList}
                >
                  <CalloutButtonWp
                    wpSlug={"pages-blacklist"}
                    info={{
                      title: "Pages Blacklist",
                      readMore: {
                        title: "Read online",
                        url: "https://www.wpo365.com/pages-blacklist/"
                      }
                    }}
                  />
                </ListEditor>
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}
          >
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Skip WordPress REST API requests</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={this.toggleSkipApiBasicAuthRequest}
                      checked={this.state.skipApiBasicAuthRequest}
                    />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={"skip-api-basic-auth-request"}
                    info={{
                      title: "Skip WordPress REST API requests (basis auth)",
                      readMore: {
                        title: "Read online",
                        url:
                          "https://www.wpo365.com/skip-api-basic-auth-request/"
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}
          >
            <Equal a={this.state.authScenario} b={AuthScenario.intranet}>
              <StyledTableRow>
                <StyledTableCell>
                  <StyledLabel>Public homepage</StyledLabel>
                </StyledTableCell>
                <StyledTableCell>
                  <StyledRowifier>
                    <StyledCheckbox>
                      <Checkbox
                        onChange={this.togglePublicHomepage}
                        checked={this.state.publicHomepage}
                      />
                    </StyledCheckbox>
                    <CalloutButtonWp
                      wpSlug={"public-homepage"}
                      info={{
                        title: "Public homepage",
                        readMore: {
                          title: "Read online",
                          url: "https://www.wpo365.com/public-homepage/"
                        }
                      }}
                    />
                  </StyledRowifier>
                </StyledTableCell>
              </StyledTableRow>
            </Equal>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}
          >
            <Equal a={this.state.authScenario} b={AuthScenario.internet}>
              <StyledTableRow>
                <StyledTableCell>
                  <StyledLabel>Private pages</StyledLabel>
                </StyledTableCell>
                <StyledTableCell>
                  <StyledRowifier>
                    <ListEditor
                      list={this.state.privatePages}
                      onListChanged={(list: string[]) =>
                        this.setState({ privatePages: list })
                      }
                      placeholder={Strings.infoPrivatePages}
                      isValid={new RegExp(/^(http|https):\/\//i)}
                      errorMessage={Strings.errPrivatePageMustStartWith}
                    >
                      <CalloutButtonWp
                        wpSlug={"private-pages"}
                        info={{
                          title: "Private Pages",
                          readMore: {
                            title: "Read online",
                            url: "https://www.wpo365.com/private-pages/"
                          }
                        }}
                      />
                    </ListEditor>
                  </StyledRowifier>
                </StyledTableCell>
              </StyledTableRow>
            </Equal>
          </VersionSwitch>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Domain whitelist</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ListEditor
                list={this.state.domainWhitelist}
                onListChanged={(list: string[]) =>
                  this.setState({ domainWhitelist: list })
                }
                placeholder={Strings.infoDomainWhiteList}
                isValid={new RegExp(/^(?!http)/i)}
                errorMessage={Strings.errWhitelistedDomainCannotStartWith}
              >
                <CalloutButtonWp
                  wpSlug={"domain-whitelist-wordpress-multisite"}
                  info={{
                    title: "Domain Whitelist (WordPress Multisite)",
                    readMore: {
                      title: "Read online",
                      url:
                        "https://www.wpo365.com/domain-whitelist-wordpress-multisite/"
                    }
                  }}
                />
              </ListEditor>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Nonce secret</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                isValid={(oldValue: any, newValue: any) => {
                  return /^$|\s/.test(newValue as string) == false;
                }}
                onValid={(newValue: any) => {
                  this.setState({
                    nonceSecret: newValue as string
                  });
                  return;
                }}
                bound={false}
                errorMessage={Strings.errNonceSecretCannotBeEmpty}
                defaultValue={this.state.nonceSecret}
                value={this.state.nonceSecret}
                placeholder={Strings.infoDirectoryId}
              >
                <CalloutButtonWp
                  wpSlug={"nonce-secret"}
                  info={{
                    title: "Nonce secret",
                    readMore: {
                      title: "Read more",
                      url: "https://www.wpo365.com/nonce-secret/"
                    }
                  }}
                />
              </ValidatableTextInput>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Remember (WordPress) user</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox
                    onChange={this.toggleRememberUser}
                    checked={this.state.rememberUser}
                  />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={"remember-user"}
                  info={{
                    title: "Remember user",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/remember-user/"
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Session Duration</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                isValid={(oldValue: any, newValue: any) => {
                  return /^\d+$/.test(newValue as string); // validate numeric input
                }}
                onValid={(newValue: any) => {
                  this.setState({
                    sessionDuration: parseInt(newValue)
                  });
                  return;
                }}
                bound={false}
                errorMessage={Strings.errSessionDurationMustBeNumeric}
                defaultValue={this.state.sessionDuration.toString()}
                value={this.state.sessionDuration.toString()}
                placeholder={Strings.infoSessionDuration}
              >
                <CalloutButtonWp
                  wpSlug={"duration-of-a-session"}
                  info={{
                    title: "Duration of a session",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/duration-of-a-session/"
                    }
                  }}
                />
              </ValidatableTextInput>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell />
            <StyledTableCell>
              <StyledRowifier>
                <PrimaryButton
                  onClick={() => this.save()}
                  iconProps={{
                    iconName:
                      this.state.testStatus == TestStatus.success
                        ? "Like"
                        : "Dislike"
                  }}
                  style={{ minWidth: "400px", minHeight: "30px" }}
                >
                  Save + Test configuration
                </PrimaryButton>
                <StyledErrorFor>
                  {errorFor(this.state.errors, "saveConfiguration").map(
                    (error: SettingsException) => (
                      <StyledP>{error.message}</StyledP>
                    )
                  )}
                </StyledErrorFor>
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
        </StyledTableRowGroup>
        <Footer />
        {this.state.additionalInfo && (
          <AdditionalInfoDialog
            onCancel={this.state.additionalInfo.onCancel}
            onConfirm={this.state.additionalInfo.onConfirm}
            onDismiss={this.state.additionalInfo.onDismiss}
            title={this.state.additionalInfo.title}
            subText={this.state.additionalInfo.subText}
          />
        )}
      </StyledTable>
    );
  }

  private toggleUseV2 = () => {
    const { useV2 } = this.state;
    if (useV2) {
      this.setState({
        useV2: false
      });
      return;
    }
    const confirm: IAdditionalInfoDialogProps = {
      title: Strings.titleUseV2,
      subText: Strings.subTextUseV2,
      onDismiss: () =>
        this.setState({
          additionalInfo: null
        }),
      onCancel: () =>
        this.setState({
          additionalInfo: null
        }),
      onConfirm: () =>
        this.setState({
          additionalInfo: null,
          useV2: true
        })
    };
    this.setState({
      additionalInfo: confirm
    });
  };

  private toggleSkipApiBasicAuthRequest = () => {
    const { skipApiBasicAuthRequest } = this.state;
    if (skipApiBasicAuthRequest) {
      this.setState({
        skipApiBasicAuthRequest: false
      });
      return;
    }
    const confirm: IAdditionalInfoDialogProps = {
      title: Strings.titleSkipApiBasicAuthRequest,
      subText: Strings.subtextSkipApiBasicAuthRequest,
      onDismiss: () =>
        this.setState({
          additionalInfo: null
        }),
      onCancel: () =>
        this.setState({
          additionalInfo: null
        }),
      onConfirm: () =>
        this.setState({
          additionalInfo: null,
          skipApiBasicAuthRequest: true
        })
    };
    this.setState({
      additionalInfo: confirm
    });
  };

  private togglePublicHomepage = () => {
    const { publicHomepage } = this.state;
    this.setState({
      publicHomepage: !publicHomepage
    });
  };

  private toggleRememberUser = () => {
    const { rememberUser } = this.state;
    this.setState({
      rememberUser: !rememberUser
    });
  };

  private testAuthWin: Window;

  private async save(): Promise<void> {
    const { authScenario } = this.state;
    const confirm: IAdditionalInfoDialogProps = {
      title: Strings.titleBeforeSave,
      subText:
        authScenario == AuthScenario.internet
          ? Strings.subTextBeforeSaveInternet
          : Strings.subTextBeforeSaveIntranet,
      onDismiss: () =>
        this.setState({
          additionalInfo: null
        }),
      onCancel: () =>
        this.setState({
          additionalInfo: null
        }),
      onConfirm: () => {
        this.onConfirm();
        this.setState({
          additionalInfo: null
        });
      }
    };
    this.setState({
      additionalInfo: confirm
    });
  }

  private async onConfirm(): Promise<void> {
    try {
      await this.saveConfiguration();
      location.hash = "selfTest";
    } catch (err) {
      this.setState({
        errors: [
          new SettingsException(
            "saveConfiguration",
            Strings.errAuthenticationTestFailed,
            2000
          )
        ],
        testStatus: TestStatus.error,
        saveStatus: SaveStatus.error
      });
      console.log(err.message);
      return;
    }
  }

  private changeAuthScenario = (
    event: React.FormEvent<HTMLDivElement>,
    item: IDropdownOption
  ) => {
    this.setState({ authScenario: item.key as AuthScenario });
  };

  private async saveConfiguration(): Promise<void> {
    this.setState({ saveStatus: SaveStatus.saving });
    const { settings } = this.props;
    settings.applicationId = this.state.applicationId;
    settings.tenantId = this.state.directoryId;
    settings.domainWhitelist = this.state.domainWhitelist;
    settings.nonceSecret = this.state.nonceSecret;
    settings.skipApiBasicAuthRequest = this.state.skipApiBasicAuthRequest;
    settings.pagesBlacklist = this.state.pagesBlacklist;
    settings.privatePages = this.state.privatePages;
    settings.redirectUrl = this.state.redirectUrl;
    settings.domainHint = this.state.domainHint;
    settings.authScenario = this.state.authScenario;
    settings.rememberUser = this.state.rememberUser;
    settings.sessionDuration = this.state.sessionDuration;
    settings.useV2 = this.state.useV2;
    settings.publicHomepage = this.state.publicHomepage;

    const updateResult = await Settings.updateSettings(
      this.props.env,
      settings
    );

    if (updateResult.success) {
      this.setState({
        errors: [],
        testStatus: TestStatus.initial,
        saveStatus: SaveStatus.success
      });
    } else {
      this.setState(
        (
          prevState: IAuthenticationTabState,
          props: IAuthenticationTabProps
        ) => {
          return {
            errors: [
              ...prevState.errors,
              new SettingsException(
                "saveConfiguration",
                updateResult.message,
                2000
              )
            ],
            testStatus: TestStatus.initial,
            saveStatus: SaveStatus.error
          };
        }
      );
    }
  }
}
