import * as React from "react";

import {
  StyledTableCaption,
  StyledTable,
  StyledTableHeader,
  StyledTableIntro,
  StyledTableRowGroup,
  StyledTableRow,
  StyledTableCell,
  StyledRowifier,
  StyledCheckbox,
  StyledTextArea
} from "./styled";

import { IEnv } from "./Wizard";
import { ValidatableTextInput } from "./ValidatableTextInput";
import { Strings } from "../strings";
import { Footer } from "./Footer";

import { Checkbox } from "office-ui-fabric-react/lib/Checkbox";
import { DefaultButton } from "office-ui-fabric-react/lib/Button";
import { IAdditionalInfoDialogProps } from "../common/IAdditionalInfoDialogProps";
import { AdditionalInfoDialog } from "./AdditionalInfoDialog";
import { Size, PluginVersion } from "../common/Enums";
import { Equal, NotEqual } from "./Equal";

export interface IEdConfiguratorProps {
  env: IEnv;
}

export interface IEdConfiguratorState {
  scriptSrc: string;
  pageSize: number;
  minQueryLength: number;
  hideSearchBox: boolean;
  hideMessageBar: boolean;
  hidePaging: boolean;
  primaryHtml: string;
  secondaryHtml: string;
  tertiaryHtml: string;
  metaHtml: string;
  showContactActionButtons: boolean;
  emailProperty?: string;
  workPhoneProperty?: string;
  imProperty?: string;
  mobilePhoneProperty?: string;
  getUserPhoto: boolean;
  queryTemplate: string;
  useDefaultSelectProperties: boolean;
  selectProperties: string;
  autoSearch: boolean;
  copyButtonCaption: string;
  templateUrl: string;
  useTemplate: boolean;
}

export class EdConfigurator extends React.Component<
  IEdConfiguratorProps,
  IEdConfiguratorState
> {
  public constructor(props: IEdConfiguratorProps) {
    super(props);
    const { env } = this.props;
    this.state = {
      scriptSrc: this.currentScriptSrc("wizard.js").replace(
        "wizard.js",
        env.props.pluginVersion === PluginVersion.wpo365LoginIntranet
          ? "ed.js"
          : "edBasic.js"
      ),
      pageSize: 10,
      minQueryLength: 3,
      hideSearchBox: false,
      hideMessageBar: false,
      hidePaging: false,
      primaryHtml: "__##displayName##__",
      secondaryHtml: "__##jobTitle##__",
      tertiaryHtml: "__##department##__",
      metaHtml: "",
      getUserPhoto: false,
      showContactActionButtons: true,
      emailProperty: "mail",
      workPhoneProperty: "businessPhones",
      imProperty: "imAddresses",
      mobilePhoneProperty: "mobilePhone",
      queryTemplate: "accountEnabled eq true and userType eq 'member'",
      useDefaultSelectProperties: true,
      selectProperties: "",
      autoSearch: false,
      // ----- //
      copyButtonCaption: Strings.capCopyShortcodeButton,
      templateUrl: this.currentScriptSrc("wizard.js").replace(
        "wizard.js",
        env.props.pluginVersion === PluginVersion.wpo365LoginIntranet
          ? "templates/ed.intranet.list.template.html"
          : ""
      ),
      useTemplate: env.props.pluginVersion === PluginVersion.wpo365LoginIntranet
    };
  }

  public render(): React.ReactElement<IEdConfiguratorProps> {
    const { env } = this.props;
    const { copyButtonCaption } = this.state;
    return (
      <StyledTable>
        <StyledTableCaption>
          <StyledTableHeader>
            Employee Directory (Microsoft Graph) shortcode generator
          </StyledTableHeader>
          <StyledTableIntro>
            To create the shortcode you can use this generator. Simply configure
            the options and copy the shortcode to the clipboard. Then all you
            need to do is to add the shortcode to a page and you're ready!
            <br />
            <br />
            Please consult the online documentation{" "}
            <a
              href={"https://www.wpo365.com/employee-directory/"}
              target={"_blank"}
            >
              https://www.wpo365.com/employee-directory/
            </a>{" "}
            for instructions on how to generate a shortcode and place it on any
            WordPress post or page.
          </StyledTableIntro>
        </StyledTableCaption>
        <StyledTableRowGroup>
          <Equal
            a={env.props.pluginVersion}
            b={PluginVersion.wpo365LoginIntranet}
          >
            <StyledTableRow>
              <StyledTableCell>
                Use (
                <a href="https://handlebarsjs.com/" target="_blank">
                  Handlebars
                </a>
                ) template
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={(event: React.MouseEvent<HTMLElement>) =>
                        this.setState((prevState: IEdConfiguratorState) => {
                          return { useTemplate: !prevState.useTemplate };
                        })
                      }
                      checked={this.state.useTemplate}
                    />
                  </StyledCheckbox>
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            {this.state.useTemplate && (
              <StyledTableRow>
                <StyledTableCell>Template</StyledTableCell>
                <StyledTableCell>
                  <ValidatableTextInput
                    size={Size.large}
                    bound={false}
                    isValid={(oldValue: any, newValue: any) => {
                      return true;
                    }}
                    onValid={(newValue: any) =>
                      this.setState({ templateUrl: newValue })
                    }
                    defaultValue={this.state.templateUrl}
                    value={this.state.templateUrl}
                  />
                </StyledTableCell>
              </StyledTableRow>
            )}
            {!this.state.useTemplate && (
              <React.Fragment>
                <StyledTableRow>
                  <StyledTableCell>Primary text</StyledTableCell>
                  <StyledTableCell>
                    <ValidatableTextInput
                      size={Size.large}
                      bound={false}
                      isValid={(oldValue: any, newValue: any) => {
                        return true;
                      }}
                      onValid={(newValue: any) =>
                        this.setState({ primaryHtml: newValue })
                      }
                      defaultValue={this.state.primaryHtml}
                    />
                  </StyledTableCell>
                </StyledTableRow>
                <StyledTableRow>
                  <StyledTableCell>Secondary text</StyledTableCell>
                  <StyledTableCell>
                    <ValidatableTextInput
                      size={Size.large}
                      bound={false}
                      isValid={(oldValue: any, newValue: any) => {
                        return true;
                      }}
                      onValid={(newValue: any) =>
                        this.setState({ secondaryHtml: newValue })
                      }
                      defaultValue={this.state.secondaryHtml}
                    />
                  </StyledTableCell>
                </StyledTableRow>
                <StyledTableRow>
                  <StyledTableCell>Tertiary text</StyledTableCell>
                  <StyledTableCell>
                    <ValidatableTextInput
                      size={Size.large}
                      bound={false}
                      isValid={(oldValue: any, newValue: any) => {
                        return true;
                      }}
                      onValid={(newValue: any) =>
                        this.setState({ tertiaryHtml: newValue })
                      }
                      defaultValue={this.state.tertiaryHtml}
                    />
                  </StyledTableCell>
                </StyledTableRow>
                <StyledTableRow>
                  <StyledTableCell>Meta text</StyledTableCell>
                  <StyledTableCell>
                    <ValidatableTextInput
                      size={Size.large}
                      bound={false}
                      isValid={(oldValue: any, newValue: any) => {
                        return true;
                      }}
                      onValid={(newValue: any) =>
                        this.setState({ metaHtml: newValue })
                      }
                      defaultValue={this.state.metaHtml}
                    />
                  </StyledTableCell>
                </StyledTableRow>
                <StyledTableRow>
                  <StyledTableCell>
                    Include contact action buttons
                  </StyledTableCell>
                  <StyledTableCell>
                    <StyledRowifier>
                      <StyledCheckbox>
                        <Checkbox
                          onChange={(event: React.MouseEvent<HTMLElement>) =>
                            this.setState((prevState: IEdConfiguratorState) => {
                              return {
                                showContactActionButtons: !prevState.showContactActionButtons
                              };
                            })
                          }
                          checked={this.state.showContactActionButtons}
                        />
                      </StyledCheckbox>
                    </StyledRowifier>
                  </StyledTableCell>
                </StyledTableRow>
                <StyledTableRow>
                  <StyledTableCell>Mobile phone property</StyledTableCell>
                  <StyledTableCell>
                    <ValidatableTextInput
                      size={Size.large}
                      bound={false}
                      isValid={(oldValue: any, newValue: any) => {
                        return true;
                      }}
                      onValid={(newValue: any) =>
                        this.setState({ mobilePhoneProperty: newValue })
                      }
                      defaultValue={this.state.mobilePhoneProperty}
                      value={this.state.mobilePhoneProperty}
                    />
                  </StyledTableCell>
                </StyledTableRow>
                <StyledTableRow>
                  <StyledTableCell>Work phone property</StyledTableCell>
                  <StyledTableCell>
                    <ValidatableTextInput
                      size={Size.large}
                      bound={false}
                      isValid={(oldValue: any, newValue: any) => {
                        return true;
                      }}
                      onValid={(newValue: any) =>
                        this.setState({ workPhoneProperty: newValue })
                      }
                      defaultValue={this.state.workPhoneProperty}
                      value={this.state.workPhoneProperty}
                    />
                  </StyledTableCell>
                </StyledTableRow>
                <StyledTableRow>
                  <StyledTableCell>IM property</StyledTableCell>
                  <StyledTableCell>
                    <ValidatableTextInput
                      size={Size.large}
                      bound={false}
                      isValid={(oldValue: any, newValue: any) => {
                        return true;
                      }}
                      onValid={(newValue: any) =>
                        this.setState({ imProperty: newValue })
                      }
                      defaultValue={this.state.imProperty}
                      value={this.state.imProperty}
                    />
                  </StyledTableCell>
                </StyledTableRow>
                <StyledTableRow>
                  <StyledTableCell>Email property</StyledTableCell>
                  <StyledTableCell>
                    <ValidatableTextInput
                      size={Size.large}
                      bound={false}
                      isValid={(oldValue: any, newValue: any) => {
                        return true;
                      }}
                      onValid={(newValue: any) =>
                        this.setState({ emailProperty: newValue })
                      }
                      defaultValue={this.state.emailProperty}
                      value={this.state.emailProperty}
                    />
                  </StyledTableCell>
                </StyledTableRow>
              </React.Fragment>
            )}
            <StyledTableRow>
              <StyledTableCell>Try retrieve user photo</StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={(event: React.MouseEvent<HTMLElement>) =>
                        this.setState((prevState: IEdConfiguratorState) => {
                          return { getUserPhoto: !prevState.getUserPhoto };
                        })
                      }
                      checked={this.state.getUserPhoto}
                    />
                  </StyledCheckbox>
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Query template</StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  bound={false}
                  isValid={(oldValue: any, newValue: any) => {
                    return true;
                  }}
                  onValid={(newValue: any) =>
                    this.setState({ queryTemplate: newValue })
                  }
                  defaultValue={this.state.queryTemplate}
                />
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Use default select properties</StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={(event: React.MouseEvent<HTMLElement>) =>
                        this.setState((prevState: IEdConfiguratorState) => {
                          return {
                            useDefaultSelectProperties: !prevState.useDefaultSelectProperties
                          };
                        })
                      }
                      checked={this.state.useDefaultSelectProperties}
                    />
                  </StyledCheckbox>
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            {!this.state.useDefaultSelectProperties && (
              <StyledTableRow>
                <StyledTableCell>
                  Select{" "}
                  <a
                    href={
                      "https://docs.microsoft.com/en-us/graph/api/resources/user?view=graph-rest-beta#properties"
                    }
                    target={"_blank"}
                  >
                    properties
                  </a>
                </StyledTableCell>
                <StyledTableCell>
                  <ValidatableTextInput
                    size={Size.large}
                    bound={false}
                    isValid={(oldValue: any, newValue: any) => {
                      return (
                        !newValue ||
                        /(^([a-z]+)$)|(^([a-z]+)(,[a-z]+)*(,[a-z]+)$)/i.test(
                          newValue as string
                        )
                      );
                    }}
                    onValid={(newValue: any) =>
                      this.setState({ selectProperties: newValue })
                    }
                    defaultValue={this.state.selectProperties}
                    errorMessage={"Comma seperated properties without a space"}
                    placeholder={"e.g. givenName,surname,jobTitle,department"}
                  />
                </StyledTableCell>
              </StyledTableRow>
            )}
            <StyledTableRow>
              <StyledTableCell>Auto search</StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={(event: React.MouseEvent<HTMLElement>) =>
                        this.setState((prevState: IEdConfiguratorState) => {
                          return { autoSearch: !prevState.autoSearch };
                        })
                      }
                      checked={this.state.autoSearch}
                    />
                  </StyledCheckbox>
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Results per page</StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  bound={false}
                  isValid={(oldValue: any, newValue: any) => {
                    const newValueAsInt = parseInt(newValue);
                    return (
                      (newValue as string) === newValueAsInt.toString() &&
                      newValueAsInt > 0 &&
                      newValueAsInt < 50
                    );
                  }}
                  onValid={(newValue: any) =>
                    this.setState({ pageSize: parseInt(newValue) })
                  }
                  errorMessage={
                    "pageSize must be number greater than 0 and smaller than 50"
                  }
                  defaultValue={"10"}
                />
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Min. input length</StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  bound={false}
                  isValid={(oldValue: any, newValue: any) => {
                    const newValueAsInt = parseInt(newValue);
                    return (
                      (newValue as string) === newValueAsInt.toString() &&
                      newValueAsInt > 0 &&
                      newValueAsInt < 5
                    );
                  }}
                  onValid={(newValue: any) =>
                    this.setState({ minQueryLength: parseInt(newValue) })
                  }
                  errorMessage={
                    "minQueryLength must be number greater than 0 and smaller than 5"
                  }
                  defaultValue={"3"}
                />
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Hide search box</StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <Checkbox
                    onChange={(event: React.MouseEvent<HTMLElement>) =>
                      this.setState((prevState: IEdConfiguratorState) => {
                        return { hideSearchBox: !prevState.hideSearchBox };
                      })
                    }
                    checked={this.state.hideSearchBox}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Hide message bar</StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <Checkbox
                    checked={this.state.hideMessageBar}
                    onChange={(event: React.MouseEvent<HTMLElement>) =>
                      this.setState((prevState: IEdConfiguratorState) => {
                        return { hideMessageBar: !prevState.hideMessageBar };
                      })
                    }
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Hide paging</StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <Checkbox
                    checked={this.state.hidePaging}
                    onChange={(event: React.MouseEvent<HTMLElement>) =>
                      this.setState((prevState: IEdConfiguratorState) => {
                        return { hidePaging: !prevState.hidePaging };
                      })
                    }
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Shortcode</StyledTableCell>
              <StyledTableCell>
                <StyledTextArea
                  readOnly={true}
                  value={`[pintra props="pageSize,${
                    this.state.pageSize
                  };minQueryLength,${
                    this.state.minQueryLength
                  };hideMessageBar,${
                    this.state.hideMessageBar ? "true" : "false"
                  };hideSearchBox,${
                    this.state.hideSearchBox ? "true" : "false"
                  };hidePaging,${
                    this.state.hidePaging ? "true" : "false"
                  };primaryHtml,${this.state.primaryHtml};secondaryHtml,${
                    this.state.secondaryHtml
                  };tertiaryHtml,${this.state.tertiaryHtml};metaHtml,${
                    this.state.metaHtml
                  };showContactActionButtons,${
                    this.state.showContactActionButtons ? "true" : "false"
                  };workPhoneProperty,${
                    this.state.workPhoneProperty
                  };mobilePhoneProperty,${
                    this.state.mobilePhoneProperty
                  };emailProperty,${this.state.emailProperty};imProperty,${
                    this.state.imProperty
                  };getUserPhoto,${
                    this.state.getUserPhoto ? "true" : "false"
                  };queryTemplate,${escape(
                    this.state.queryTemplate
                  )};selectProperties,${
                    !this.state.useDefaultSelectProperties
                      ? this.state.selectProperties
                          .trim()
                          .split(",")
                          .join("|")
                      : ""
                  };autoSearch,${
                    this.state.autoSearch ? "true" : "false"
                  };useTemplate,${
                    this.state.useTemplate ? "true" : "false"
                  };templateUrl,${this.state.templateUrl}" script_url="${
                    this.state.scriptSrc
                  }"]`}
                  className={"pintraSpoShortCode"}
                />
              </StyledTableCell>
            </StyledTableRow>
          </Equal>
          <NotEqual
            a={env.props.pluginVersion}
            b={PluginVersion.wpo365LoginIntranet}
          >
            <StyledTableRow>
              <StyledTableCell>Shortcode</StyledTableCell>
              <StyledTableCell>
                <StyledTextArea
                  readOnly={true}
                  value={`[pintra props="" script_url="${this.state.scriptSrc}"]`}
                  className={"pintraSpoShortCode"}
                />
              </StyledTableCell>
            </StyledTableRow>
          </NotEqual>
          <StyledTableRow>
            <StyledTableCell />
            <StyledTableCell>
              <StyledRowifier>
                <DefaultButton
                  onClick={this.copyShortcode}
                  iconProps={{ iconName: "Copy" }}
                  style={{ minWidth: "400px", minHeight: "30px" }}
                >
                  {copyButtonCaption}
                </DefaultButton>
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
        </StyledTableRowGroup>
        <Footer />
        {/* this.state.additionalInfo && (
          <AdditionalInfoDialog
            onCancel={this.state.additionalInfo.onCancel}
            onConfirm={this.state.additionalInfo.onConfirm}
            onDismiss={this.state.additionalInfo.onDismiss}
            title={this.state.additionalInfo.title}
            subText={this.state.additionalInfo.subText}
          />
        ) */}
      </StyledTable>
    );
  }

  private copyShortcode = () => {
    (document.getElementsByClassName(
      "pintraSpoShortCode"
    )[0] as HTMLTextAreaElement).select();
    document.execCommand("copy");
    this.setState({
      copyButtonCaption: Strings.capCopyShortcodeButtonSuccess
    });
    setTimeout(
      () =>
        this.setState({ copyButtonCaption: Strings.capCopyShortcodeButton }),
      3000
    );
  };

  private currentScriptSrc = (scriptName: string): string | null => {
    const scriptTags = document.getElementsByTagName("script");
    for (let i = scriptTags.length - 1; i >= 0; i--) {
      if (scriptTags[i].src.indexOf(scriptName) > -1) {
        return scriptTags[i].src;
      }
    }
    return null;
  };
}
