import * as React from "react";

import { StyledInputText, StyledErrorFor, StyledRowifier } from "./styled";
import { Size } from "../common/Enums";

export interface IValidatableProps {
  size: Size;
  defaultValue: string; // The initial value
  value?: string; // Value that can be updated from an higher level component
  disabled?: boolean;
  bound: boolean; // If bound any validation error is expected to flow back to the parent
  errorMessage?: string;
  onError?: (errorMessage: string) => void;
  isValid?: (oldValue: any, newValue: any) => boolean;
  onValid?: (newValue: any) => void;
  placeholder?: string;
}

export interface IValidatableState {
  valid: boolean;
  value: string;
}

export class ValidatableTextInput extends React.Component<
  IValidatableProps,
  IValidatableState
> {
  public constructor(props: IValidatableProps) {
    super(props);
    this.state = {
      value: this.props.defaultValue,
      valid: this.props.isValid
        ? this.props.isValid(this.props.defaultValue, this.props.defaultValue)
        : true
    };
  }

  public componentDidUpdate(
    prevProps: IValidatableProps,
    prevState: IValidatableState
  ) {
    if (this.props.value !== prevProps.value) {
      this.onChange(this.props.value);
    }
  }

  private onChange = (newValue: any) => {
    const { errorMessage, onError, isValid, onValid } = this.props;
    if (isValid) {
      if (isValid(this.state.value, newValue)) {
        this.setState({ value: newValue, valid: true });
        onValid(newValue);
      } else {
        this.setState({ value: newValue, valid: false });
        if (onError) onError(errorMessage ? errorMessage : "");
      }
    } else if (onValid) onValid(newValue);
    else this.setState({ value: newValue as string });
  };

  public render() {
    return (
      <StyledRowifier>
        <StyledInputText
          minWidth={this.props.size}
          type={"text"}
          onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
            this.onChange(event.target.value);
          }}
          value={this.state.value}
          style={
            this.state.valid
              ? { border: "1px solid #dadada" }
              : { border: "1px solid red" }
          }
          disabled={this.props.disabled}
          placeholder={
            !!this.props.placeholder ? this.props.placeholder : null
          }
        />
        {this.props.children}
        {!this.props.bound ? (
          <StyledErrorFor>
            {!this.state.valid ? this.props.errorMessage : ""}
          </StyledErrorFor>
        ) : null}
      </StyledRowifier>
    );
  }
}
