import * as React from "react";

import {
  StyledTable,
  StyledTableCaption,
  StyledTableHeader,
  StyledTableIntro,
  StyledTableRowGroup,
  StyledTableRow,
  StyledTableCell,
  StyledLabel,
  StyledRowifier,
  StyledErrorFor,
  StyledP,
  StyledCheckbox,
  StyledListWrapper,
  StyledListMenu,
  StyledListMain,
  StyledSelfTestResultItemWrapper,
  StyledSelfTestResultItemCell,
  StyledSettings,
  StyledSelfTestResultItemKey,
  StyledSelfTestResultItemValue
} from "./styled";

import { Strings } from "../strings";
import { CalloutButtonWp } from "./CalloutButtonWp";
import { IEnv } from "./Wizard";
import { Settings } from "../common/Settings";
import { SettingsException } from "../common/Exceptions";
import { SaveStatus, Size } from "../common/Enums";
import { Footer } from "./Footer";
import {
  PrimaryButton,
  CommandBarButton,
  IButtonStyles
} from "office-ui-fabric-react/lib/Button";
import { errorFor } from "../common/Helpers";
import Axios from "axios";
import { AdditionalInfoDialog } from "./AdditionalInfoDialog";
import { IAdditionalInfoDialogProps } from "../common/IAdditionalInfoDialogProps";
import { Checkbox } from "office-ui-fabric-react/lib/Checkbox";
import {
  OverflowSet,
  IOverflowSetItemProps
} from "office-ui-fabric-react/lib/OverflowSet";
import { ISelfTestResult } from "../common/ISelfTestResult";
import { SelfTestResultDialog } from "./SelfTestResultDialog";

const buttonStyles: Partial<IButtonStyles> = {
  root: {
    minWidth: 0,
    padding: "10px",
    alignSelf: "stretch",
    height: "auto"
  }
};

export interface ISelfTestTabProps {
  env: IEnv;
  settings: Settings;
}

export interface ISelfTestTabState {
  testMode: boolean;
  testAccesToken: boolean;
  testConfiguration: boolean;
  testIdToken: boolean;
  selfTestResults: ISelfTestResult[];
  filteredSelfTestResults: ISelfTestResult[];
  selfTestResult: ISelfTestResult;
  //-----
  copying: boolean;
  copyButtonCaption: string;
  errors: SettingsException[];
  saveStatus: SaveStatus;
  additionalInfo: IAdditionalInfoDialogProps | null;
}

export class SelfTestTab extends React.Component<
  ISelfTestTabProps,
  ISelfTestTabState
> {
  private selfTestResultsTextArea: any;

  public constructor(props: ISelfTestTabProps) {
    super(props);
    const { settings } = this.props;
    this.state = {
      testMode: settings.testMode,
      testAccesToken: settings.testAccessToken,
      testConfiguration: settings.testConfiguration,
      testIdToken: settings.testIdToken,
      selfTestResults: [],
      filteredSelfTestResults: [],
      selfTestResult: undefined,
      copyButtonCaption: Strings.capCopyLogButton,
      copying: false,
      errors: [],
      saveStatus: SaveStatus.initial,
      additionalInfo: null
    };
    this.getSelfTestResults();
  }

  public componentDidUpdate(prevProps: ISelfTestTabProps): void {
    if (this.props.settings === prevProps.settings) return;
    const { settings } = this.props;
    this.setState({
      testMode: settings.testMode,
      testConfiguration: settings.testConfiguration,
      testAccesToken: settings.testAccessToken
    });
  }

  public render(): React.ReactElement<ISelfTestTabProps> {
    const { selfTestResults, filteredSelfTestResults, copying } = this.state;
    return (
      <StyledTable>
        <StyledTableCaption>
          <StyledTableHeader>Plugin self-test</StyledTableHeader>
          <StyledTableIntro
            dangerouslySetInnerHTML={{
              __html: `Use this tab to test the plugin and its ability to sign you / your users in with Microsoft. As soon as you click
              "Start self-test" the plugin will automatically activate "Test mode" (you cannot activate "Test mode" manually) and try to 
              sign the current user in with Microsoft by redirecting the user to Microsoft's login page. When redirected back to your 
              website the plugin will intercept the authentication response (ID token) and analyze its contents. Furthermore the plugin 
              may request an access token (if you checked the "Test access token(s)" option) and will analyze the assigned static permissions
              needed for normal operation of the plugin and optional Graph / SharePoint integration. Last but not least some essential 
              configuration validation checks will be performed if you checked the "Test configuration" option. As soon as the test is completed
              the plugin will automatically de-activate "Test mode".<br/><br/>
              <strong>Please note</strong><ul><li>During the self-test (when the "Test mode" is activated) the plugin is not protecting your 
              website</li><li>You can click on each entry in the list below to view the full details incl. category, severity, message and
              - whenever available - a link for further information.</li><li>During the self-test this or other pages may be reloaded</li></ul>`
            }}
          />
        </StyledTableCaption>
        <StyledTableRowGroup>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Test mode</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox disabled={true} checked={this.state.testMode} />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={"plugin-self-test"}
                  info={{
                    title: "Plugin self-test",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/plugin-self-test/"
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Test ID token</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox disabled={true} checked={this.state.testIdToken} />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={"plugin-self-test"}
                  info={{
                    title: "Plugin self-test",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/plugin-self-test/"
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Test access token(s)</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox
                    onChange={this.toggleTestAccessToken}
                    checked={this.state.testAccesToken}
                  />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={"plugin-self-test"}
                  info={{
                    title: "Plugin self-test",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/plugin-self-test/"
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Test configuration</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox
                    onChange={this.toggleTestConfiguration}
                    checked={this.state.testConfiguration}
                  />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={"plugin-self-test"}
                  info={{
                    title: "Plugin self-test",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/plugin-self-test/"
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell />
            <StyledTableCell>
              <StyledRowifier>
                <PrimaryButton
                  onClick={() =>
                    this.setState({ testMode: true }, () =>
                      this.saveConfiguration()
                    )
                  }
                  iconProps={{
                    iconName:
                      this.state.saveStatus == SaveStatus.success
                        ? "Like"
                        : "Dislike"
                  }}
                  style={{ minWidth: "400px", minHeight: "30px" }}
                  disabled={this.state.saveStatus == SaveStatus.saving}
                >
                  Start self-test
                </PrimaryButton>
                <StyledErrorFor>
                  {errorFor(this.state.errors, "saveConfiguration").map(
                    (error: SettingsException) => (
                      <StyledP>{error.message}</StyledP>
                    )
                  )}
                  {this.state.saveStatus == SaveStatus.success
                    ? Strings.okSettingsUpdated
                    : null}
                </StyledErrorFor>
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>&nbsp;</StyledTableCell>
            <StyledTableCell>&nbsp;</StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Self-test result:</StyledLabel>
            </StyledTableCell>
            <StyledTableCell style={{ maxWidth: "700px" }}>
              <StyledListWrapper>
                <StyledListMenu>
                  <OverflowSet
                    items={[
                      {
                        key: "filterNotPassed",
                        name: "Not passed",
                        icon: "Filter",
                        ariaLabel: "Filter: Not passed",
                        onClick: () =>
                          this.filterSelfTestResults(
                            (selfTestResult: ISelfTestResult) =>
                              !selfTestResult.passed
                          )
                      },
                      {
                        key: "deleteFilter",
                        name: "Show all",
                        icon: "ClearFilter",
                        ariaLabel: "Delete filters",
                        onClick: () =>
                          this.setState({
                            filteredSelfTestResults: selfTestResults
                          })
                      },
                      {
                        key: "copyClipboard",
                        name: "Copy to clipboard",
                        icon: "Copy",
                        ariaLabel: "Copy to clipboard",
                        onClick: this.copyToClipboard
                      }
                    ]}
                    overflowItems={[]}
                    onRenderOverflowButton={this.onRenderOverflowButton}
                    onRenderItem={this.onRenderItem}
                  />
                </StyledListMenu>
                <StyledListMain>
                  <StyledSelfTestResultItemWrapper>
                    {filteredSelfTestResults.map((item, index) =>
                      this.selfTestResultListCell(item, index)
                    )}
                  </StyledSelfTestResultItemWrapper>
                </StyledListMain>
              </StyledListWrapper>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow style={{ display: copying ? "inherit" : "none" }}>
            <StyledTableCell>
              <StyledLabel>Full self-test results (for copying):</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledSettings
                ref={(textarea: any) =>
                  (this.selfTestResultsTextArea = textarea)
                }
                readOnly={true}
                value={JSON.stringify(selfTestResults, null, 2)}
              />
            </StyledTableCell>
          </StyledTableRow>
        </StyledTableRowGroup>
        <Footer />
        {this.state.additionalInfo && (
          <AdditionalInfoDialog
            onCancel={this.state.additionalInfo.onCancel}
            onConfirm={this.state.additionalInfo.onConfirm}
            onDismiss={this.state.additionalInfo.onDismiss}
            title={this.state.additionalInfo.title}
            subText={this.state.additionalInfo.subText}
          />
        )}
        {this.state.selfTestResult && (
          <SelfTestResultDialog
            onDismiss={() => this.setState({ selfTestResult: undefined })}
            selfTestResult={this.state.selfTestResult}
          />
        )}
      </StyledTable>
    );
  }

  private toggleTestConfiguration = () => {
    const { testConfiguration } = this.state;
    this.setState({
      testConfiguration: !testConfiguration
    });
  };

  private toggleTestAccessToken = () => {
    const { testAccesToken } = this.state;
    this.setState({
      testAccesToken: !testAccesToken
    });
  };

  private async saveConfiguration(): Promise<void> {
    this.setState({ saveStatus: SaveStatus.saving });
    const { settings } = this.props;
    settings.testMode = this.state.testMode;
    settings.testAccessToken = this.state.testAccesToken;
    settings.testConfiguration = this.state.testConfiguration;
    const updateResult = await Settings.updateSettings(
      this.props.env,
      settings
    );

    if (updateResult.success) {
      this.setState(
        {
          errors: [],
          saveStatus: SaveStatus.success
        },
        () => {
          if (this.state.testMode) {
            this.startSelfTest();
          }
        }
      );
    } else {
      this.setState(
        (prevState: ISelfTestTabState, props: ISelfTestTabProps) => {
          return {
            errors: [
              ...prevState.errors,
              new SettingsException(
                "saveConfiguration",
                updateResult.message,
                2000
              )
            ],
            saveStatus: SaveStatus.error
          };
        }
      );
    }
  }

  private startSelfTest(): void {
    const thisWindow = window as any;
    thisWindow.wpo365 = thisWindow.wpo365 || {};
    const wpo365OpenIdRedirectDiv = document.createElement("div");
    wpo365OpenIdRedirectDiv.id = "wpo365OpenIdRedirect";
    document.body.appendChild(wpo365OpenIdRedirectDiv);
    const redirectTo = `${window.location.href.split("#")[0]}#selfTest`;
    try {
      thisWindow.wpo365.pintraRedirect.toMsOnline("", redirectTo);
    } catch (err) {
      console.log("Error occured whilst trying to redirect to MS online");
    }
  }

  private async getSelfTestResults(): Promise<ISelfTestResult[]> {
    const env = this.props.env;
    const data = new FormData();
    data.append("action", "get_self_test_results");
    data.append("nonce", env.nonce);

    let response: any;
    try {
      response = await Axios.post(env.wpAjaxAdminUrl, data);
    } catch (error) {
      console.log(error);
      return null;
    }
    if (response.status == 200) {
      if (
        response.data &&
        response.data.status &&
        response.data.status == "OK"
      ) {
        try {
          const selfTestResults = JSON.parse(
            response.data.result
          ) as ISelfTestResult[];
          this.setState({ selfTestResults }, () =>
            this.filterSelfTestResults(
              (selfTestResult: ISelfTestResult) => true
            )
          );
        } catch (error) {
          console.log(error);
          return null;
        }
      }
    }
    return null;
  }

  private filterSelfTestResults = (
    filter: (selfTestResult: ISelfTestResult) => boolean
  ): void => {
    const { selfTestResults } = this.state;
    const filteredSelfTestResults = selfTestResults.filter(filter);
    this.setState({ filteredSelfTestResults });
  };

  private selfTestResultListCell = (
    item: ISelfTestResult,
    index: number | undefined
  ): JSX.Element => (
    <React.Fragment data-is-focusable={true}>
      {index == 0 && (
        <StyledSelfTestResultItemCell>
          <StyledSelfTestResultItemKey
            style={{ whiteSpace: "nowrap", minWidth: "150px" }}
          >
            Timestamp
          </StyledSelfTestResultItemKey>
          <StyledSelfTestResultItemKey
            style={{
              minWidth: "calc(100% - 210px)",
              maxWidth: "calc(100% - 210px)"
            }}
          >
            Title
          </StyledSelfTestResultItemKey>
          <StyledSelfTestResultItemKey style={{ maxWidth: "50px" }}>
            Passed
          </StyledSelfTestResultItemKey>
        </StyledSelfTestResultItemCell>
      )}
      <StyledSelfTestResultItemCell
        onClick={(ev: React.MouseEvent<HTMLDivElement>) =>
          this.showDetailedSelfTestResult(item)
        }
        style={{ cursor: "pointer" }}
      >
        <StyledSelfTestResultItemValue
          style={{ whiteSpace: "nowrap", minWidth: "150px" }}
          dangerouslySetInnerHTML={{ __html: item.timestamp }}
        />
        <StyledSelfTestResultItemValue
          style={{
            minWidth: "calc(100% - 210px)",
            maxWidth: "calc(100% - 210px)"
          }}
          dangerouslySetInnerHTML={{ __html: item.title }}
        />
        <StyledSelfTestResultItemValue
          style={{ maxWidth: "50px" }}
          dangerouslySetInnerHTML={{ __html: item.passed ? "Yes" : "No" }}
        />
      </StyledSelfTestResultItemCell>
    </React.Fragment>
  );

  private onRenderItem = (item: IOverflowSetItemProps): JSX.Element => {
    if (item.onRender) {
      return item.onRender(item);
    }
    return (
      <CommandBarButton
        iconProps={{ iconName: item.icon }}
        menuProps={item.subMenuProps}
        text={item.name}
        onClick={item.onClick}
        styles={buttonStyles}
      />
    );
  };

  private onRenderOverflowButton = (
    overflowItems: any[] | undefined
  ): JSX.Element => {
    return (
      <CommandBarButton
        styles={buttonStyles}
        menuIconProps={{ iconName: "More" }}
        menuProps={{ items: overflowItems! }}
      />
    );
  };

  private copyToClipboard = (
    event: React.MouseEvent<HTMLButtonElement>
  ): void => {
    this.setState({ copying: true }, () => {
      this.selfTestResultsTextArea.select();
      document.execCommand("copy");
      this.setState({
        copyButtonCaption: Strings.capCopyLogButtonSuccess,
        copying: false
      });
      setTimeout(
        () => this.setState({ copyButtonCaption: Strings.capCopyLogButton }),
        3000
      );
    });
  };

  private showDetailedSelfTestResult = (
    selfTestResult: ISelfTestResult
  ): void => {
    this.setState({ selfTestResult });
  };
}
