import * as React from "react";
import { IEnv } from "./Wizard";
import { Settings } from "../common/Settings";
import {
  StyledTable,
  StyledTableCaption,
  StyledTableHeader,
  StyledTableIntro,
  StyledTableRowGroup,
  StyledTableRow,
  StyledTableCell,
  StyledLabel,
  StyledRowifier,
  StyledCheckbox,
  StyledErrorFor,
  StyledP
} from "./styled";
import { Footer } from "./Footer";
import { Checkbox } from "office-ui-fabric-react/lib/Checkbox";
import { CalloutButtonWp } from "./CalloutButtonWp";
import { ValidatableTextInput } from "./ValidatableTextInput";
import { Size, SaveStatus, PluginVersion } from "../common/Enums";
import { Strings } from "../strings";
import { PrimaryButton } from "office-ui-fabric-react/lib/Button";
import { SettingsException } from "../common/Exceptions";
import { errorFor } from "../common/Helpers";
import { VersionSwitch } from "./VersionSwitch";
import { KeyValuePair } from "../common/KeyValuePair";
import { KeyValueEditor } from "./KeyValueEditor";
import { IAdditionalInfoDialogProps } from "../common/IAdditionalInfoDialogProps";
import { AdditionalInfoDialog } from "./AdditionalInfoDialog";
import { Equal } from "./Equal";

export interface ILoginLogoutBehavourTabProps {
  env: IEnv;
  settings: Settings;
}

export interface ILoginLogoutBehavourTabState {
  redirectToLogin: boolean;
  redirectToLoginV2: boolean;
  customLoginUrl: string;
  errorPageUrl: string;
  enableSingleSignOut: boolean;
  hideSsoLink: boolean;
  interceptWpLogin: boolean;
  gotoAfterSignonUrl: string;
  alwaysUseGotoAfter: boolean;
  logoutFromO365: boolean;
  postSignoutUrl: string;
  groupsXGotoAfter: KeyValuePair[];
  errors: SettingsException[];
  saveStatus: SaveStatus;
  additionalInfo: IAdditionalInfoDialogProps | null;
}

export class LoginLogoutBehavourTab extends React.Component<
  ILoginLogoutBehavourTabProps,
  ILoginLogoutBehavourTabState
> {
  public constructor(props: ILoginLogoutBehavourTabProps) {
    super(props);
    const { settings } = this.props;
    this.state = {
      redirectToLogin: settings.redirectToLogin,
      redirectToLoginV2: settings.redirectToLoginV2,
      customLoginUrl: settings.customLoginUrl,
      errorPageUrl: settings.errorPageUrl,
      enableSingleSignOut: settings.enableSingleSignOut,
      hideSsoLink: settings.hideSsoLink,
      interceptWpLogin: settings.interceptWpLogin,
      gotoAfterSignonUrl: settings.gotoAfterSignonUrl,
      alwaysUseGotoAfter: settings.alwaysUseGotoAfter,
      logoutFromO365: settings.logoutFromO365,
      postSignoutUrl: settings.postSignoutUrl,
      groupsXGotoAfter: settings.groupsXGotoAfter,
      errors: [],
      saveStatus: SaveStatus.initial,
      additionalInfo: null
    };
  }

  public componentDidUpdate(prevProps: ILoginLogoutBehavourTabProps): void {
    if (this.props.settings === prevProps.settings) return;
    const { settings } = this.props;
    this.setState({
      redirectToLogin: settings.redirectToLogin,
      redirectToLoginV2: settings.redirectToLoginV2,
      customLoginUrl: settings.customLoginUrl,
      errorPageUrl: settings.errorPageUrl,
      enableSingleSignOut: settings.enableSingleSignOut,
      hideSsoLink: settings.hideSsoLink,
      interceptWpLogin: settings.interceptWpLogin,
      gotoAfterSignonUrl: settings.gotoAfterSignonUrl,
      alwaysUseGotoAfter: settings.alwaysUseGotoAfter,
      logoutFromO365: settings.logoutFromO365,
      postSignoutUrl: settings.postSignoutUrl,
      groupsXGotoAfter: settings.groupsXGotoAfter
    });
  }

  public render(): React.ReactElement<ILoginLogoutBehavourTabProps> {
    return (
      <StyledTable>
        <StyledTableCaption>
          <StyledTableHeader>
            Login / Logout / Error behaviour
          </StyledTableHeader>
          <StyledTableIntro
            dangerouslySetInnerHTML={{
              __html: `Use this tab to configure
            <ol><li>The page where to redirect the user to when an error occurs when signin in</li>
            <li>When an Office 365 user tries to sign in manually,</li>
            <li>Where to redirect the user when he / she signed in successfully (by the default the user will be 
            redirected back to the page he / she visited before the plugin sent him / her to Microsoft to sign in),</li>
            <li>When a user signs out of your WordPress website</li></ol>`
            }}
          />
        </StyledTableCaption>
        <StyledTableRowGroup>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Intercept manual logins</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox
                    onChange={this.toggleInterceptWpLogin}
                    checked={this.state.interceptWpLogin}
                  />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={"intercept-manual-logins"}
                  info={{
                    title: "Intercept manual logins",
                    readMore: {
                      title: "Read online",
                      url: "https://www.wpo365.com/intercept-manual-logins/"
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Goto after</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                isValid={(oldValue: any, newValue: any) => {
                  if ((newValue as string) != Strings.empty)
                    return /^(http|https):\/\//i.test(newValue as string);
                  return true;
                }}
                onValid={(newValue: any) => {
                  this.setState({
                    gotoAfterSignonUrl: newValue as string
                  });
                  return;
                }}
                bound={false}
                errorMessage={Strings.errGotoAfterSignonUrlMustStartWith}
                defaultValue={this.state.gotoAfterSignonUrl}
                value={this.state.gotoAfterSignonUrl}
                placeholder={Strings.infoGotoAfterSignonUrl}
              >
                <CalloutButtonWp
                  wpSlug={"goto-after"}
                  info={{
                    title: "Goto after",
                    readMore: {
                      title: "Read more",
                      url: "https://www.wpo365.com/goto-after/"
                    }
                  }}
                />
              </ValidatableTextInput>
            </StyledTableCell>
          </StyledTableRow>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}
          >
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Always goto after</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={this.toggleAlwaysUseGotoAfter}
                      checked={this.state.alwaysUseGotoAfter}
                    />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={"always-use-goto-after"}
                    info={{
                      title: "Always goto after",
                      readMore: {
                        title: "Read online",
                        url: "https://www.wpo365.com/always-use-goto-after/"
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}
          >
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Enable logout without confirmation</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={this.toggleEnableSingleSignOut}
                      checked={this.state.enableSingleSignOut}
                    />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={"enable-logout-without-confirmation"}
                    info={{
                      title: "Enable logout without confirmation",
                      readMore: {
                        title: "Read online",
                        url:
                          "https://www.wpo365.com/enable-logout-without-confirmation/"
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}
          >
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Logout from O365</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={this.toggleLogoutFromO365}
                      checked={this.state.logoutFromO365}
                    />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={"logout-from-o365"}
                    info={{
                      title: "Logout from O365",
                      readMore: {
                        title: "Read online",
                        url: "https://www.wpo365.com/logout-from-o365/"
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}
          >
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Post sign-out URL</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  isValid={(oldValue: any, newValue: any) => {
                    if ((newValue as string) != Strings.empty)
                      return /^(http|https):\/\//i.test(newValue as string);
                    return true;
                  }}
                  onValid={(newValue: any) => {
                    this.setState({
                      postSignoutUrl: newValue as string
                    });
                    return;
                  }}
                  bound={false}
                  errorMessage={Strings.errPostSignoutUrlMustStartWith}
                  defaultValue={this.state.postSignoutUrl}
                  value={this.state.postSignoutUrl}
                  placeholder={Strings.infoPostSignoutUrl}
                >
                  <CalloutButtonWp
                    wpSlug={"post-sign-out-url"}
                    info={{
                      title: "Post sign-out URL",
                      readMore: {
                        title: "Read more",
                        url: "https://www.wpo365.com/post-sign-out-url/"
                      }
                    }}
                  />
                </ValidatableTextInput>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}
          >
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Azure AD Group Membership Redirection</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <KeyValueEditor
                    list={this.state.groupsXGotoAfter}
                    onListChanged={(list: KeyValuePair[]) =>
                      this.setState({ groupsXGotoAfter: list })
                    }
                    keyPlaceholder={Strings.infoGroupsXGotoAfterKey}
                    keyIsValid={
                      new RegExp(
                        /^[0-9a-f]{8}-[0-9a-f]{4}-[1-5][0-9a-f]{3}-[89ab][0-9a-f]{3}-[0-9a-f]{12}$/i
                      )
                    }
                    keyErrorMessage={Strings.errGroupsXGotoAfterKey}
                    valuePlaceholder={Strings.infoGroupsXGotoAfterValue}
                    valueIsValid={new RegExp(/^(http|https):\/\//i)}
                    valueErrorMessage={Strings.errGroupsXGotoAfterValue}
                  >
                    <CalloutButtonWp
                      wpSlug={"groups-goto-after"}
                      info={{
                        title: "Azure AD Group Membership Redirection",
                        readMore: {
                          title: "Read online",
                          url: "https://www.wpo365.com/groups-goto-after/"
                        }
                      }}
                    />
                  </KeyValueEditor>
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Error page URL</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                isValid={(oldValue: any, newValue: any) => {
                  if ((newValue as string) != Strings.empty)
                    return /^(http|https):\/\//i.test(newValue as string);
                  return true;
                }}
                onValid={(newValue: any) => {
                  this.setState({
                    errorPageUrl: newValue as string
                  });
                  return;
                }}
                bound={false}
                errorMessage={Strings.errErrorPageUrlMustStartWith}
                defaultValue={this.state.errorPageUrl}
                value={this.state.errorPageUrl}
                placeholder={Strings.infoErrorPageUrl}
              >
                <CalloutButtonWp
                  wpSlug={"error-page"}
                  info={{
                    title: "Error page URL",
                    readMore: {
                      title: "Read more",
                      url: "https://www.wpo365.com/error-page/"
                    }
                  }}
                />
              </ValidatableTextInput>
            </StyledTableCell>
          </StyledTableRow>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}
          >
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Custom login URL</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  isValid={(oldValue: any, newValue: any) => {
                    if ((newValue as string) != Strings.empty)
                      return /^(http|https):\/\//i.test(newValue as string);
                    return true;
                  }}
                  onValid={(newValue: any) => {
                    this.setState({
                      customLoginUrl: newValue as string
                    });
                    return;
                  }}
                  bound={false}
                  errorMessage={Strings.errCustomLoginUrlMustStartWith}
                  defaultValue={this.state.customLoginUrl}
                  value={this.state.customLoginUrl}
                  placeholder={Strings.infoCustomLoginUrl}
                >
                  <CalloutButtonWp
                    wpSlug={"custom-login-url"}
                    info={{
                      title: "Custom login URL",
                      readMore: {
                        title: "Read more",
                        url: "https://www.wpo365.com/custom-login-url/"
                      }
                    }}
                  />
                </ValidatableTextInput>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}
          >
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Dual login (deprecated)</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={this.toggleRedirectToLogin}
                      checked={this.state.redirectToLogin}
                    />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={"redirect-to-login"}
                    info={{
                      title: "Redirect to login",
                      readMore: {
                        title: "Read online",
                        url: "https://www.wpo365.com/redirect-to-login/"
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}
          >
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Dual login V2</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={this.toggleRedirectToLoginV2}
                      checked={this.state.redirectToLoginV2}
                    />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={"redirect-to-login"}
                    info={{
                      title: "Redirect to login V2",
                      readMore: {
                        title: "Read online",
                        url: "https://www.wpo365.com/redirect-to-login/"
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}
          >
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Hide SSO link</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={this.toggleHideSsoLink}
                      checked={this.state.hideSsoLink}
                    />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={"hide-sso-link"}
                    info={{
                      title: "Hide SSO link",
                      readMore: {
                        title: "Read online",
                        url: "https://www.wpo365.com/hide-sso-link/"
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <StyledTableRow>
            <StyledTableCell />
            <StyledTableCell>
              <StyledRowifier>
                <PrimaryButton
                  onClick={() => this.saveConfiguration()}
                  iconProps={{
                    iconName:
                      this.state.saveStatus == SaveStatus.success
                        ? "Like"
                        : "Dislike"
                  }}
                  style={{ minWidth: "400px", minHeight: "30px" }}
                  disabled={this.state.saveStatus == SaveStatus.saving}
                >
                  Save configuration
                </PrimaryButton>
                <StyledErrorFor>
                  {errorFor(this.state.errors, "saveConfiguration").map(
                    (error: SettingsException) => (
                      <StyledP>{error.message}</StyledP>
                    )
                  )}
                  {this.state.saveStatus == SaveStatus.success
                    ? Strings.okSettingsUpdated
                    : null}
                </StyledErrorFor>
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
        </StyledTableRowGroup>
        <Footer />
        {this.state.additionalInfo && (
          <AdditionalInfoDialog
            onCancel={this.state.additionalInfo.onCancel}
            onConfirm={this.state.additionalInfo.onConfirm}
            onDismiss={this.state.additionalInfo.onDismiss}
            title={this.state.additionalInfo.title}
            subText={this.state.additionalInfo.subText}
          />
        )}
      </StyledTable>
    );
  }

  private toggleRedirectToLogin = (): void => {
    const { redirectToLogin } = this.state;
    if (redirectToLogin) {
      this.setState({
        redirectToLogin: false
      });
      return;
    }
    const confirm: IAdditionalInfoDialogProps = {
      title: Strings.titleRedirectToLogin,
      subText: Strings.subTextRedirectToLogin,
      onDismiss: () =>
        this.setState({
          additionalInfo: null
        }),
      onCancel: () =>
        this.setState({
          additionalInfo: null
        }),
      onConfirm: () =>
        this.setState({
          additionalInfo: null,
          redirectToLogin: true,
          redirectToLoginV2: false
        })
    };
    this.setState({
      additionalInfo: confirm
    });
  };

  private toggleRedirectToLoginV2 = (): void => {
    this.setState(
      (
        prevState: ILoginLogoutBehavourTabState,
        props: ILoginLogoutBehavourTabProps
      ) => {
        return {
          redirectToLoginV2: !prevState.redirectToLoginV2,
          redirectToLogin: false
        };
      }
    );
  };

  private toggleHideSsoLink = (): void => {
    const { hideSsoLink } = this.state;
    if (hideSsoLink) {
      this.setState({
        hideSsoLink: false
      });
      return;
    }
    const confirm: IAdditionalInfoDialogProps = {
      title: Strings.titleHideSsoLink,
      subText: Strings.subTextHideSsoLink,
      onDismiss: () =>
        this.setState({
          additionalInfo: null
        }),
      onCancel: () =>
        this.setState({
          additionalInfo: null
        }),
      onConfirm: () =>
        this.setState({
          additionalInfo: null,
          hideSsoLink: true
        })
    };
    this.setState({
      additionalInfo: confirm
    });
  };

  private toggleEnableSingleSignOut = (): void => {
    this.setState(
      (
        prevState: ILoginLogoutBehavourTabState,
        props: ILoginLogoutBehavourTabProps
      ) => {
        return { enableSingleSignOut: !prevState.enableSingleSignOut };
      }
    );
  };

  private toggleInterceptWpLogin = (): void => {
    this.setState(
      (
        prevState: ILoginLogoutBehavourTabState,
        props: ILoginLogoutBehavourTabProps
      ) => {
        return { interceptWpLogin: !prevState.interceptWpLogin };
      }
    );
  };

  private toggleAlwaysUseGotoAfter = (): void => {
    this.setState(
      (
        prevState: ILoginLogoutBehavourTabState,
        props: ILoginLogoutBehavourTabProps
      ) => {
        return { alwaysUseGotoAfter: !prevState.alwaysUseGotoAfter };
      }
    );
  };

  private toggleLogoutFromO365 = (): void => {
    this.setState(
      (
        prevState: ILoginLogoutBehavourTabState,
        props: ILoginLogoutBehavourTabProps
      ) => {
        return { logoutFromO365: !prevState.logoutFromO365 };
      }
    );
  };

  private async saveConfiguration(): Promise<void> {
    this.setState({ saveStatus: SaveStatus.saving });
    const { settings } = this.props;
    settings.redirectToLogin = this.state.redirectToLogin;
    settings.redirectToLoginV2 = this.state.redirectToLoginV2;
    settings.hideSsoLink = this.state.hideSsoLink;
    settings.customLoginUrl = this.state.customLoginUrl;
    settings.errorPageUrl = this.state.errorPageUrl;
    settings.enableSingleSignOut = this.state.enableSingleSignOut;
    settings.interceptWpLogin = this.state.interceptWpLogin;
    settings.gotoAfterSignonUrl = this.state.gotoAfterSignonUrl;
    settings.alwaysUseGotoAfter = this.state.alwaysUseGotoAfter;
    settings.postSignoutUrl = this.state.postSignoutUrl;
    settings.logoutFromO365 = this.state.logoutFromO365;
    settings.groupsXGotoAfter = this.state.groupsXGotoAfter;
    const updateResult = await Settings.updateSettings(
      this.props.env,
      settings
    );

    if (updateResult.success) {
      this.setState({
        errors: [],
        saveStatus: SaveStatus.success
      });
    } else {
      this.setState(
        (
          prevState: ILoginLogoutBehavourTabState,
          props: ILoginLogoutBehavourTabProps
        ) => {
          return {
            errors: [
              ...prevState.errors,
              new SettingsException(
                "saveConfiguration",
                updateResult.message,
                2000
              )
            ],
            saveStatus: SaveStatus.error
          };
        }
      );
    }
  }
}
